import React, {useEffect, useState} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSpinner} from "@fortawesome/free-solid-svg-icons";
import Restaurant from "./Restaurant";





function App() {

  const [menuList, setMenuList] = useState([]);
  const [loading, setLoading] = useState(true);

    useEffect(() => {
       fetch('/api').then(res => res.json())
           .then(data => {
               console.log(data)
               setMenuList((data))
               setLoading(false)
           })
   }, [])


  return (loading ? // palauttaa spinner iconin jos backend hidastelee ja sit palauttaa noi ravintolat
          <div>
          <FontAwesomeIcon className="spinner" icon={faSpinner} size="6x" flip="vertical" color="white" spin/>
          </div>
          :
          <div>
              <div className="info">
                  <h1>Tarjolla tänään</h1>
                  <p>{new Date().toLocaleDateString('fi')} ruokalistat</p>

              </div>

          <div className="restaurant-box">
          {menuList.map(menu =>
          <Restaurant {...menu} key={menu["RestaurantName"]} menuList={menuList} setMenuList={setMenuList}  />)}
      </div>
              <footer>
                  <p>Sivusto ei vastaa tietojen oikeellisuudesta eikä ole tekemisissä Unican tai sen yhteistyökumppaneiden kanssa. Varmista kaikki tiedot suoraan ravintoloilta.</p>
                  <p>© 2020 M.T</p>
              </footer>
          </div>

);


}

export default App;


